import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // loading
    loading: false,
    // 
    requests: []
  },
  mutations: {
    loadStatus(state, boolean) {
      state.loading = boolean
    },
    addRequest(state, string) {
      state.requests.push(string)
    }
  }
})
